.App{
    text-align: center;
    font-size: 25px;
    margin-top: 30px;
    margin-left: 5px;
    margin-right: 5px;
}

.App-title{
    font-size: 40px;
    font-family: 'Pacifico', cursive;
}

.button{
    margin: 5px auto;
    width: 170px;
}

.form-menu{
    margin-top: 15px;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}
.quotes-group{
    display: block;
    text-align: left;
    margin: 0 auto;
    font-size: 17px;
    max-width: 847px;
}

.quotes-item{
    font-size: 16px;
    margin-bottom: 1px;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
    text-decoration: none;
    padding-right: 85px;

    -moz-transition: all 1s ease-out;
    -o-transition: all 1s ease-out;
    -webkit-transition: all 1s ease-out;
    cursor: pointer;
}

.quotes-item.selected-item{
    background: blanchedalmond;
}

.disabled{
    visibility: hidden;
}

.quotes-item:hover {
    background-color: #3d5061;
    color: #ffffff;
    -webkit-transform: scale(1.05);
    -moz-transform: scale(1.05);
    -o-transform: scale(1.05);
}


.quote-text{
    width: 100%;
}

.quotes-buttons{
    top: 50%;
    right: 4px;
    position: absolute;
    margin-top: -15px;
}

.quotes-button{
    margin-right: 3px;
    margin-left: 3px;
}

.modal-button{
    width: 100px;
}


